@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

:root {
  --primary-color: #FF3364;
  --secondary-color: #F2F2F2;
  --gray-color: #747474;
  --title-size: 32px;
  --header-size: 24px;
  --action-size: 14px;
  --body-size: 14px;
}

body {
  font-family: 'Poppins', sans-serif;
}

.wrapper {
  max-width: 1200px;
  margin: auto;
  padding: 0 1.5rem;
  box-sizing: border-box;
}

@media (max-width: 1024px) {
  :root {
    --title-size: 28px;
    --header-size: 20px;
  }
}

@media (max-width: 768px) {
  :root {
    --title-size: 26px;
    --header-size: 18px;
    --action-size: 12px;
    --body-size: 12px;
  }
}