.navbar {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 20;
}

.container {
    max-width: 1800px;
    padding: 1rem 4rem;
    margin: auto;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.container .brand h2 {
    font-size: var(--header-size);
    color: var(--primary-color);
}

.container .link {
    display: flex;
    align-items: center;
    column-gap: 20px;
}

.container .link a {
    font-size: var(--action-size);
    text-decoration: none;
    color: #000;
}

.container .action button {
    padding: 10px 30px;
    box-sizing: border-box;
    background-color: transparent;
    border: 1.5px solid var(--primary-color);
    border-radius: 10px;
    font-size: var(--body-size);
    font-weight: 600;
    color: var(--primary-color);
}

.navbar_mobile {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 20;
    display: none;
}

.navbar_mobile .container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5rem 1.5rem;
    box-sizing: border-box;
}

.navbar_mobile .container h2 {
    font-size: var(--header-size);
    color: var(--primary-color);
}

.navbar_mobile .container i {
    font-size: 24px;
    color: var(--primary-color);
}

@media (max-width: 768px) {
    .navbar {
        display: none;
    }

    .navbar_mobile {
        display: block;
    }
}