.articles {
    padding: 4rem 0;
}

.articles .header {
    padding: 2rem 0;
}

.articles .header h2 {
    font-size: var(--header-size);
}

.articles .container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 50px;
    row-gap: 80px;
}

@media (max-width: 1024px) {
    .articles .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 30px;
        row-gap: 60px;
    }
}

@media (max-width: 768px) {
    .articles .container {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        row-gap: 50px;
    }
}