.hero {
    width: 100%;
    background-color: var(--primary-color);
}

.container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.container .content h1 {
    font-size: var(--title-size);
    color: white;
}

.container .content p {
    font-size: calc(var(--header-size) - 6px);
    font-weight: 400;
    margin-top: 10px;
    width: 80%;
    color: white;
}

.container .content button {
    margin-top: 50px;
    background-color: white;
    padding: 15px 20px;
    box-sizing: border-box;
    border: none;
    border-radius: 10px;
    font-size: var(--body-size);
    font-weight: 600;
    color: var(--primary-color);
}

.container .image img {
    width: 100%;
}

@media (max-width: 768px) {
    .container {
        flex-direction: column-reverse;
        padding: 4rem 0;
    }

    .container .content p {
        width: 80%;
    }

    .container .image img {
        display: none;
    }
}