.card {
    background-color: white;
}

.card img {
    width: 100%;
    object-fit: cover;
    max-height: 300px;
    border-radius: 10px;
}

.card .container {
    padding: 10px 15px;
    box-sizing: border-box;
}

.card h2 {
    font-size: var(--body-size);
    margin-bottom: 10px;
}

.card p {
    font-size: calc(var(--body-size) - 2px);
    margin-bottom: 20px;
}

.card a {
    font-size: var(--body-size);
    text-decoration: none;
    color: var(--primary-color);
}