.features {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(4, 1fr);
    padding: 2rem 0;
    column-gap: 50px;
}

.features .item {
    display: flex;
    align-items: center;
    column-gap: 20px;
    padding: 10px 20px;
    box-sizing: border-box;
    background-color: white;
    box-shadow: 0px 3px 27px -10px rgba(150, 170, 180, 0.7);
    border-radius: 10px;
}

.features .item img {
    width: 50px;
    height: 50px;
}

.features .item p {
    font-size: var(--body-size);
}

@media (max-width: 768px) {
    .features {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    .features .item img {
        width: 30px;
        height: 30px;
    }
}